<template>
    <v-main class="pa-0">

        <v-card class="mx-auto" elevation="4" style="width: 70%;">
            <v-row class="ma-1">
                <v-col cols="12">
                    <input type="file" ref="fileInput" style="display: none" @change="handleFileUpload" />
                    <v-btn height="70px" color="#454545" @click="openFileInput"><p style="color: white;"> 사진추가 </p></v-btn>
                    <v-btn height="70px" class="pressed ml-4" color="#E76161" :border="bord" v-model="isButtonPressed" @click="changeButton">
                        <p style="color: white;">사진삭제</p></v-btn>
                    <v-icon  class="mdi mdi-delete-circle ml-4" v-if="isButtonPressed" size="50px" ></v-icon>
                </v-col>

            </v-row>

            <v-row class="ma-5 bg-blue-grey-lighten-5"  >
                    <v-col v-for="(image,i) in images" :key="i" cols="12" sm="6" md="4">
                        <v-card>
                            <v-img :src=image @click="deleteImage(image)"></v-img>

                        </v-card>
                    </v-col>
                </v-row>
        </v-card>
    </v-main>
</template>

<script>
export default {
  mounted () {
    this.updateImage()
  },
  data: () => ({
    images: [],
    isButtonPressed: false
  }),
  methods: {
    openFileInput () {
      this.$refs.fileInput.click()
    },
    handleFileUpload (event) {
      return new Promise((resolve, reject) => {
        const file = event.target.files[0]

        const reader = new FileReader()
        reader.onload = function (e) {
          const fileData = e.target.result

          // 파일 데이터를 로컬 스토리지에 저장
          localStorage.setItem('image' + Date.now(), fileData)

          // 파일 읽기가 완료되었으므로 Promise를 resolve 합니다.
          resolve()
        }

        reader.onerror = function (e) {
          // 에러 발생 시 Promise를 reject 합니다.
          reject(e)
        }

        reader.readAsDataURL(file)
      }).then(() => {
        // 파일 읽기가 완료되었으므로 updateImage 메소드를 호출합니다.
        this.updateImage()
      }).catch(error => {
        // 에러 처리를 수행합니다.
        console.error('Error reading file:', error)
      })
    },
    updateImage () {
      this.images = []
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i)

        console.log(i)
        if (key.slice(0, 5) === 'image') {
          this.images.push(localStorage.getItem(key))
        }
      }
    },

    changeButton () {
      if (this.isButtonPressed === true) {
        this.isButtonPressed = false
      } else {
        this.isButtonPressed = true
      }
    },
    deleteImage (e) {
      if (this.isButtonPressed === true) {
        const index = this.images.indexOf(e)
        this.images.splice(index, 1)
        for (let i = 0; i < localStorage.length; i++) {
          const key = localStorage.key(i)

          if (localStorage.getItem(key) === e) {
            localStorage.removeItem(key)
          }
        }
      }
    }
  }
}

</script>

